.main-top{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #000;
  color: #fff;
  margin-bottom: 1em;
  z-index: 110;
  .ed-item{
    padding: 0 1em;
  }
  p{
    @include to(l){
      text-align: center;
    }
    &::before{
      position: relative;
      top: .2em;
      margin-right: .4em;
    }
  }
}
.city-phone{
  display: none;
  margin: 0;
  padding: .5em 0;
  &.show{
    display: block;
  }
  a{
    margin-left: .5em;
    color: green;
  }
  .whatsapp::before{
    position: relative;
    top: 3px;
    margin-right: .5em;
  }
}
.qrt{
  font-size: .9em;
  padding: 0;
  line-height: 2em;
}
