body {
  margin: 0;
  font-family: "Nunito Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Baloo", cursive;
  font-weight: 600;
  margin: 0;
}

h1 {
  font-size: 1.7em;
  line-height: 2em;
}

h2 {
  font-size: 1.5em;
  line-height: 1.8em;
}

h3 {
  font-size: 1.3em;
  line-height: 1.6em;
}

h4 {
  font-size: 1.2em;
  line-height: 1.5em;
}

p {
  font-size: 1em;
  line-height: 1.1em;
  margin: 0;
  margin-bottom: .5em;
}

p:last-child, p:nth-child(only) {
  margin-bottom: 0;
}

.btn {
  padding: 0 1em;
  line-height: 2.5em;
  border-radius: 8px;
  cursor: pointer;
}

.main-menu {
  width: 100%;
}

.main-menu, .main-menu ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.main-menu li {
  position: relative;
  list-style: none;
  flex: auto;
}

.main-menu li.parent-submenu {
  display: flex;
  flex-wrap: wrap;
}

.main-menu li.parent-submenu a {
  flex: auto;
}

.main-menu ul {
  display: none;
}

@media screen and (max-width: 939px) {
  .main-menu ul.show-submenu {
    display: block;
    width: 100%;
    order: 3;
    margin-left: 1em;
    margin-bottom: .5em;
  }
}

.main-menu a {
  display: block;
  line-height: 3em;
  padding: 0 1em;
}

.main-menu a:hover {
  text-decoration: none;
}

@media screen and (min-width: 940px) {
  .main-menu {
    display: flex;
    align-items: center;
  }
  .main-menu a {
    text-align: center;
  }
  .main-menu .expand {
    display: none;
  }
  .main-menu ul {
    position: absolute;
    left: 0;
    top: 100%;
    min-width: 100%;
    white-space: nowrap;
  }
  .main-menu ul a {
    text-align: left;
  }
  .main-menu ul ul {
    top: 0;
    left: 100%;
  }
  .main-menu li:hover > ul {
    display: block;
  }
}

.main-menu .expand-submenu {
  position: relative;
  right: 0;
  width: 3em;
  height: 3em;
  cursor: pointer;
  transition: all .3s;
}

@media screen and (min-width: 940px) {
  .main-menu .expand-submenu {
    display: none;
  }
}

.main-menu .expand-submenu::after {
  content: "";
  position: absolute;
  width: 40%;
  height: 40%;
  top: 20%;
  left: 30%;
  border-left: 0.45em solid;
  border-bottom: 0.45em solid;
  border-radius: 0.24em;
  transform: rotate(-45deg);
  transition: all .3s;
}

.main-menu .expand-submenu.active {
  transform: rotate(180deg);
}

@media screen and (max-width: 939px) {
  .main-menu {
    position: absolute;
    width: 100%;
    max-height: 75vh;
    left: 0;
    background: #004297;
    box-shadow: 2px 2px 5px rgba(68, 68, 68, 0.5);
    transition: top .3s;
    display: none;
  }
}

@media screen and (max-width: 939px) {
  .main-menu li {
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  }
  .main-menu li:first-child {
    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
}

.main-menu a {
  padding: 0 2em;
  color: #fff;
  text-transform: uppercase;
  font-size: 1.1em;
  font-weight: 600;
}

.fixed .main-menu a {
  text-shadow: none;
}

.main-nav-toggle {
  position: relative;
  z-index: 300;
  width: 3em;
  height: 2em;
  box-shadow: 0 0.4em #fff inset;
  cursor: pointer;
}

.main-nav-toggle::after, .main-nav-toggle::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.4em;
  background-color: #fff;
  bottom: 0;
}

.main-nav-toggle::after {
  bottom: 0.8em;
}

@media screen and (min-width: 940px) {
  .main-nav-toggle {
    display: none;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20000;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__container {
  width: 90% !important;
  position: relative;
  max-width: 600px;
  height: auto;
  margin: 0 auto;
  width: auto;
}

.modal__container img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.modal__close {
  height: 3rem;
  width: 3rem;
  position: absolute;
  right: 0;
  top: 0;
  transform: translate(0, -100%);
  cursor: pointer;
  background-color: transparent;
  transition: background-color .3s;
}

@media screen and (min-width: 640px) {
  .modal__close {
    transform: translate(calc(100% + 1em), calc((100% + 1em) * -1));
  }
}

.modal__close:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.modal__close::before, .modal__close::after {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 2px;
  transform-origin: center;
  background-color: #fff;
}

.modal__close::before {
  transform: translateX(-50%) rotate(-45deg);
}

.modal__close::after {
  transform: translateX(-50%) rotate(45deg);
}

.main-header {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: var(--header-height);
  max-height: var(--header-height);
  background-color: rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(248, 248, 248, 0.5);
  transition: max-height .5s, background-color .5s;
}

.main-header.stícky {
  position: fixed;
  top: 0;
  background: red;
}

.main-header.fixed {
  position: fixed;
  top: 0 !important;
  --header-height: 90px;
  background-color: #004297;
  border-color: rgba(68, 68, 68, 0.5);
}

.logo {
  height: var(--header-height);
}

.main-logo {
  transform: scale(1);
  transition: transform .2s;
}

.fixed .main-logo {
  transform: scale(0.6);
}

.menu-container a {
  transition: color .5s;
}

.logo-link {
  transition: transform .2s .8s;
  height: 80%;
  display: flex;
  align-items: center;
}

.logo-link img {
  transition: transform .2s .8s;
  display: block;
  height: 100%;
}

.tar {
  text-align: right;
}

.logo_aadoccsa {
  height: 50px;
  transition: fill 5s;
}

.logo_aadoccsa .cls-1 {
  fill: #fff;
  stroke: rgba(255, 255, 255, 0.2);
  opacity: .75;
}

.logo-let .cls-1 {
  fill: #111;
}

.banner-home {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(17, 17, 17, 0.35), rgba(17, 17, 17, 0.35)), url("../images/banner1.jpg") right center/cover fixed no-repeat;
}

@media screen and (max-width: 1023px) {
  .banner-home {
    background-position: right;
  }
}

.banner-caption {
  position: relative;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 600px;
}

@media screen and (min-width: 1420px) {
  .banner-caption {
    max-width: 700px;
  }
}

.logo-york1 {
  width: 40%;
  height: 200px;
}

@media screen and (min-width: 1024px) {
  .logo-york1 {
    transform: rotateX(10%);
  }
}

.logo-let {
  width: 40%;
  height: 150px;
  animation: aparecer2 1s forwards;
}

@keyframes aparecer2 {
  to {
    opacity: 1;
    left: 0;
  }
}

.logo-york-img {
  max-height: 150px;
  animation: aparecer 1s forwards;
  opacity: 0;
}

@keyframes aparecer {
  to {
    opacity: 1;
    left: 0;
  }
}

.logo-cap h1 {
  text-align: center;
  color: #FFF;
}

.banner-med {
  height: 200px;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../images/banner2.jpg") center/cover no-repeat fixed;
  clip-path: polygon(0 15%, 100% 0, 100% 85%, 0 100%);
}

.dist {
  position: absolute;
  display: table;
  padding: .3em 1em;
  background: rgba(0, 0, 0, 0.75);
  font-size: 1.1em;
  top: -2em;
  color: #fff;
}

section {
  padding-top: 100px;
  padding-bottom: 2em;
}

.section-title {
  align-self: center;
  text-align: center;
  font-size: 2.5em;
}

@media screen and (max-width: 939px) {
  .products__container {
    padding: 0 1em;
  }
}

.products__gallery {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1em;
  margin-right: -1em;
}

.products__gallery img {
  display: block;
}

body {
  overflow-x: hidden;
}

.products__gallery > .products__item {
  width: calc(100% - 2em);
  margin: 1em;
}

@media screen and (min-width: 640px) {
  .products__gallery > .products__item {
    width: calc(50% - 2em);
  }
}

@media screen and (min-width: 1024px) {
  .products__gallery > .products__item {
    width: calc(33.33333% - 2em);
  }
}

@supports (grid-template-areas: "header") {
  .products__gallery {
    display: grid;
    grid-gap: 2em;
    margin-left: 0;
    margin-right: 0;
    grid-template-columns: repeat(1, 1fr);
  }
  body {
    overflow-x: visible;
  }
  @media screen and (min-width: 640px) {
    .products__gallery {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media screen and (min-width: 1024px) {
    .products__gallery {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  .products__gallery > .products__item {
    width: 100%;
    margin: 0;
  }
}

.card {
  width: 100%;
  position: relative;
  overflow: hidden;
  margin: 0;
  border: 1px solid #888;
  padding: 1em 1em 2em;
}

.card figcaption {
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
  padding: 1em .5em;
  text-align: center;
  left: 0;
  font-size: .8em;
  text-transform: uppercase;
  width: 100%;
  color: #fff;
}

.card .over {
  width: 100%;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.card .over [class*='icon-'] {
  display: flex;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  border-radius: 50%;
  position: absolute;
  background: rgba(0, 66, 151, 0.75);
  color: #FFFFFF;
  transition: all .3s;
  opacity: 0;
}

.card .over [class*='icon-']:hover {
  text-decoration: none;
  background: #004297;
}

.card .over .icon-pdf {
  left: -30%;
}

.card .over .icon-pdf::before {
  position: relative;
  top: 0;
}

.card .over .icon-zoom {
  right: -30%;
}

.card:hover [class*='icon-'] {
  opacity: 1;
}

.card:hover .icon-pdf {
  left: 20%;
}

.card:hover .icon-zoom {
  right: 20%;
}

@media screen and (max-width: 939px) {
  .us__container {
    padding: 0 1em;
  }
}

.us h3 {
  margin-top: 2em;
  margin-bottom: 1em;
}

.contact__form {
  width: 100%;
  position: relative;
}

@media screen and (min-width: 1024px) {
  .contact__form {
    width: 60%;
  }
}

@media screen and (max-width: 639px) {
  .contact__form {
    margin-bottom: 2em;
  }
}

.contact__data {
  width: 100%;
}

@media screen and (min-width: 1024px) {
  .contact__data {
    width: 40%;
  }
}

.contact__data p {
  line-height: 1.3em;
}

.contact__data span {
  margin-right: 5px;
}

.contact__data a {
  color: inherit;
}

.input-group {
  line-height: 2rem;
  position: relative;
  margin-bottom: 1em;
  overflow: hidden;
}

.input-group input, .input-group textarea {
  border-radius: .5em;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  font-size: 1em;
  outline: none;
  font-family: #004297 !important;
  border: 5px solid rgba(0, 66, 151, 0.5);
}

.input-group input {
  line-height: 2rem;
  padding-left: 1em;
  padding-right: 1em;
}

.input-group label {
  position: absolute;
  height: 2rem;
  line-height: 2rem;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.input-group textarea {
  display: block;
  resize: none;
  padding: 1em;
  color: red;
  font-family: "Nunito Sans", sans-serif;
  font-size: 1em;
}

.input-group textarea + label {
  top: 3px;
  transform: none;
}

.full textarea {
  height: 5rem;
}

.full label {
  display: none;
}

.send {
  border: none;
  text-transform: uppercase;
  background: rgba(0, 66, 151, 0.8);
  color: #fff;
  transition: all .3s;
}

.send:hover {
  background: #004297;
}

.btn-container {
  display: flex;
  justify-content: flex-end;
}

.contact__form [required]:valid {
  border: 1px solid rgba(0, 128, 0, 0.3);
  color: #111111;
}

.contact__form [required]:invalid {
  border: 1px solid rgba(255, 0, 0, 0.3);
  color: rgba(255, 0, 0, 0.6);
}

.contact__form [required]:focus {
  border: 1px solid #004297;
  outline: none;
}

.contact__form *::-webkit-input-placeholder {
  color: #444;
  font-family: #004297;
}

.result {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 128, 0, 0.75);
  padding: 1em;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
}

.result .msg-success, .result .msg-error {
  font-size: 2em;
  color: rgba(255, 255, 255, 0.85);
  text-align: center;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.result .msg-error {
  display: none;
}

.result.error {
  background: rgba(255, 0, 0, 0.75);
}

.result.error .msg-success {
  display: none;
}

.result.error .msg-error {
  display: block;
}

.box-container {
  width: 100%;
  margin: 0 auto 0;
  display: grid;
  grid-template-columns: 170px 1fr;
  grid-gap: 1em;
}

.box-container p {
  margin-bottom: 0;
}

.phone-li {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.phone-li li::after {
  content: '/';
  margin: 0 .5em;
}

.phone-li li:last-child::after, .phone-li li:only-child::after {
  display: none;
}

.city {
  font-size: .9em;
  text-align: right;
}

.main-footer {
  color: #fff;
  background: #333;
}

.main-footer__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  height: 3rem;
}

.main-footer p {
  margin: 0;
  width: 100%;
}

@media screen and (min-width: 640px) {
  .main-footer p {
    width: 50%;
  }
}

.main-footer p:last-child {
  text-align: center;
}

@media screen and (min-width: 640px) {
  .main-footer p:last-child {
    text-align: right;
  }
}

@media screen and (max-width: 639px) {
  .main-footer p {
    font-size: .8em;
    text-align: center;
  }
  .main-footer p:last-child {
    margin-top: .3em;
  }
}

.main-footer a {
  color: rgba(255, 255, 255, 0.75);
  transition: all .3s;
  transform: scale(1);
}

.main-footer a:hover {
  color: lightcyan;
  text-decoration: none;
  transform: scale(5);
}

#year {
  margin-right: 5px;
  margin-left: 5px;
}

.main-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #000;
