}

.main-top .ed-item {
  padding: 0 1em;
}

@media screen and (max-width: 1023px) {
  .main-top p {
    text-align: center;
  }
}

.main-top p::before {
  position: relative;
  top: .2em;
  margin-right: .4em;
}

.city-phone {
  display: none;
  margin: 0;
  padding: .5em 0;
}

.city-phone.show {
  display: block;
}

.city-phone a {
  margin-left: .5em;
  color: green;
}

.city-phone .whatsapp::before {
  position: relative;
  top: 3px;
  margin-right: .5em;
}

.qrt {
  font-size: .9em;
  padding: 0;
  line-height: 2em;
}

[class^="icon-"]::before {
  position: relative;
  top: .5em;
}
