.contact{
  @include edContainer;
  &__form{
    @include edItem(100,100,60);
    position: relative;
    @include to(m){
      margin-bottom: 2em;
    }
  }
  &__data{
    @include edItem(100,100,40);
    p{
      line-height: 1.3em;
    }
    span{
      margin-right: 5px;
    }
    a{
      color: inherit;
    }
  }

}
$height: 2rem;
.input-group{

  line-height: $height;
  position: relative;
  margin-bottom: 1em;
  //border: 1px solid rgba($main-color,.5);
  overflow: hidden;

  input, textarea{
    border-radius: .5em;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 1em;
    //border: none;
    outline: none;
    font-family: $main-color !important;
    border: 5px solid rgba($main-color,.5) ;
  }
  input{
    line-height: $height;
    padding-left: 1em;
    padding-right: 1em;
  }
  label{
    position: absolute;
    height: $height;
    line-height: $height;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
  };
  textarea{
    display: block;
    resize: none;
    padding: 1em;
    color: red;
    font-family: $main-font;
    font-size: 1em;
  }
  textarea + label{
    top: 3px;
    transform: none;
  }
}
.full{
  textarea{
    height: $height * 2.5;;
  }
  label{
    display: none;
  }
}
.send{
  border: none;
  text-transform: uppercase;
  background: rgba($main-color,.8);
  color: #fff;
  transition: all .3s;
  &:hover{
    background: rgba($main-color,1);
  }
}
.btn-container{
  display: flex;
  justify-content: flex-end;
}
.contact__form{
  & [required]:valid{
    border:1px solid rgba(green,.3);
    color: #111111;
  }
  & [required]:invalid{
    border:1px solid rgba(red,.3);
    color: rgba(red,.6);
  }
  & [required]:focus{
    border:1px solid rgba($main-color,5);
    outline: none;
  }
  & *::-webkit-input-placeholder{
    color: #444;
    font-family: $main-color;
  }
}
.result{
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(green,.75);
  padding: 1em;
  border-radius: 1rem;
  //display: flex;
  justify-content: center;
  align-items: center;
  .msg-success, .msg-error{
    font-size: 2em;
    color: rgba(#fff,.85);
    text-align: center;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }
  .msg-error{
    display: none;
  }
  &.error{
    background: rgba(red,.75);
    .msg-success{
      display: none;
    }
    .msg-error{
      display: block;
    }
  }
  //background-clip: content-box;
}

.box-container{
  width: 100%;
  margin: 0 auto 0;
  display: grid;
  grid-template-columns: 170px 1fr;
  grid-gap: 1em;
  p{
    margin-bottom: 0;
  }
}
.phone-li{
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  
  li{
      &::after{
      content: '/';
      margin: 0 .5em;
    }
    &:last-child, &:only-child{
      &::after{
        display: none;
      }
    }
  }
  
}
.city{
  font-size: .9em;
  text-align: right;
}
