.main-footer{
  color: #fff;
  background: #333;
  &__content{
    @include edContainer;
    @include crossCenter;
    height: 3rem;
  }
  p{
    margin: 0;
    @include edItem(100,50);
    &:last-child{
      text-align: center;
      @include from(m){
        text-align: right;
      }
    }
    @include to(m){
      font-size: .8em;
      text-align: center;
      &:last-child{
        margin-top: .3em;
      }
    }
  }
  a{
    color: rgba(#fff,.75);
    transition: all .3s;
    transform: scale(1);
    &:hover{
      color: lightcyan;
      text-decoration: none;
      transform: scale(5);
    }
  }
}
#year{
  margin-right: 5px;
  margin-left: 5px;
}
