.card{
  width: 100%;
  position: relative;
  overflow: hidden;
  margin: 0;
  border: 1px solid #888;
  padding: 1em 1em 2em;
  figcaption{
    position: absolute;
    bottom: 0;
    background: rgba(#000,.75);
    padding: 1em .5em;
    text-align: center;
    left: 0;
    font-size: .8em;
    text-transform: uppercase;
    //padding: .5em 5px;
    width: 100%;
    color: #fff;
  }
  .over{
    $size: 60px;
    width: 100%;
    height: $size;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    & [class*='icon-']{
      display: flex;
      width: $size;
      height: $size;
      justify-content: center;
      align-items: center;
      font-size: $size*.5;
      border-radius: 50%;
      position: absolute;
      background: rgba($main-color,.75);
      color: #FFFFFF;
      transition: all .3s;
      opacity: 0;
      &:hover{
        text-decoration: none;
        background: $main-color;
      }
    }
    & .icon-pdf{
      left: -30%;
      &::before{
        position: relative;
        top: 0;
      }
    }
    & .icon-zoom{
      right: -30%;
    }
  }

}
.card:hover{
  & [class*='icon-']{
    opacity: 1;
  }
  & .icon-pdf{
    left: 20%;
  }
  & .icon-zoom{
    right: 20%;
  }
}
