
.main-header{
  position: absolute;
  z-index: 100;
  width: 100%;
  height: var(--header-height);
  max-height: var(--header-height);
  background-color: rgba(#000,.5);
  border-bottom: 1px solid rgba(darken($secondColor,2),.5);
  transition: max-height .5s, background-color .5s ;
  &.stícky{
    position: fixed;
    top: 0;
    background: red;
  }
  &.fixed{
    position: fixed;
    top: 0 !important;
    //background: red;
    --header-height: 90px;
    background-color: $main-color;
    border-color: rgba(#444,.5);
  }
}
.logo{
  height: var(--header-height);
}
.main-logo{
  transform: scale(1);
  transition: transform .2s ;
  .fixed &{
    transform: scale(.6);
  }
}
.menu-container{
  a{
    transition: color .5s;
  }
  .fixed & a{
    //color: $third-color;
  }
}
.logo{
  &-link{
    //display: block;
    transition: transform .2s .8s;
    height: 80%;
    display: flex;
    align-items: center;
    img{
      transition: transform .2s .8s;
      display: block;
      height: 100%;
    }
  }
}
.tar{
  text-align: right;
}
