.modal{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20000;
    width: 100%;
    min-height: 100vh;
    background-color: rgba(#000,.9);
    display: flex;
    justify-content: center;
    align-items: center;
    &__container{
        width: 90% !important;
        position: relative;
        max-width: 600px;
        height: auto;
        margin: 0 auto;
        width: auto;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: center;
        }
    }
    &__close{
        $size: 3rem;
        $pos: 0;
        height: $size;
        width: $size;
        // background: red;
        position: absolute;
        right: $pos*-1;
        top: $pos;
        transform: translate(0,-100%);
        cursor: pointer;
        background-color: transparent;
        transition: background-color .3s;
        @include from(m){
            transform: translate(calc(100% + 1em), calc((100% + 1em) * -1))
        }
        &:hover{
            background-color: rgba(#000,.3);
        }
        &::before, &::after{
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            height: 100%;
            width: 2px;
            transform-origin: center;
            background-color: #fff;
        }
        &::before{
            transform: translateX(-50%) rotate(-45deg);
            
        }
        &::after{
            transform: translateX(-50%) rotate(45deg);

        }
    }
}