/*
  This boilerplate is based in ITCSS and SMACSS
*/
$cssCore    : false  ; //Compila todo el css (no recomendado si se trabaja con sass).
$cssHelpers : true  ; //Ayudas extras para el layout (para alinear elementos, limpiar los floats, controlar el padding, etc...) Se recomienda activarlo.
$devMode    : false  ; //Ayuda visual que nos permite ver en el navegador breakpoints, el tamaño de los elementos, el area disponible etc... 


:root{
  --header-height: 150px;
}

@import "ed-grid/ed-grid";

// Config
@import "theme/typography";
@import "config/functions";
@import "config/variables";
@import "config/mixins";
@import "config/colors";

// Base and elements (generic)
@import "base/base";

// LAYOUT
  // Escribe solo los estilos de layout (division en regiones del viewport)
@import "layout/layout";

// MODULOS (SMACSS) O COMPONENTES (ITCSS)
  // Son los elementos de UI de tu interfaz, como botones, tablas, menus, etc.
  // Crea los archivos adicionales que necesites (uno por componente)
@import "components/buttons";
@import "components/menus";
@import "components/modal";

// Theme (colors and fonts)

@import "theme/header";
@import "theme/logo";
@import "theme/banner.scss";
@import "theme/sections.scss";
@import "theme/products";
@import "theme/us";
@import "theme/contact";
@import "theme/footer";
@import "theme/top";

// Utilities (hacks)
  // Todos los hacks que hayas necesitado para solucionar conflictos
  // o resolver situaciones extremas. Se espera poder mejorarlos luego.
@import "hacks/hacks";
