.banner-home{
  $lg: rgba(#111,.35);
  width: 100%;
  height: 100vh;
  background: linear-gradient($lg,$lg), url('../images/banner1.jpg') right center / cover fixed no-repeat;
  @include to(l){
    background-position: right;
  }
}

.banner-caption{
  position: relative;
  z-index: 1;
  //background: #000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 600px;
  @include from(1420px){
    max-width: 700px;
  }
}

.logo-container{
  //background: red;
  //position: relative;
  //display: flex;
  //flex-wrap: nowrap;
  //justify-content: center;
}
.logo-york1{
  width: 40%;
  height: 200px;
  @include from(l){
    transform: rotateX(10%);
  }
}
.logo-let {
  width: 40%;
  height: 150px;
  animation: aparecer2 1s forwards;
  //transform: translateX(5px);
  @include to(m){

  }
  @keyframes aparecer2 {
    to {
      opacity: 1;
      left: 0;
    }

  }
}
.logo-york-img{
  max-height: 150px;
  animation: aparecer 1s forwards;
  opacity: 0;
  @keyframes aparecer {
    to{
      opacity: 1;
      left: 0;
    }

  }
}
.logo-cap{
  h1{
    text-align: center;
    color: #FFF;
  }
}
.banner-med{
  $lg: rgba(#000,.5);
  height: 200px;
  background: linear-gradient($lg,$lg),
              url("../images/banner2.jpg") center / cover no-repeat fixed;
  clip-path: polygon(0 15%, 100% 0, 100% 85%, 0 100%) ;

}
.dist{
  position: absolute;
  display: table;
  padding: .3em 1em;
  background: rgba(#000,.75);
  //right: 0;
  font-size: 1.1em;
  top: -2em;
  color: #fff;
}
