.main-menu{
  @include edMenu($bp);

  @include to($bp){
    position: absolute;
    width: 100%;
    max-height: 75vh;
    left: 0;
    background: $main-color;
    box-shadow: 2px 2px 5px rgba(#444,.5);
    transition: top .3s;
    display: none;
  }
  li{
    //border-right: 1px solid #fff;
    @include to($bp){
      border-bottom: 1px solid rgba(#fff,.5);
      &:first-child{
        border-top: 1px solid rgba(#fff,.5);
      }
    }
  }
  a{
    padding: 0 2em;
    color: #fff;
    text-transform: uppercase;
    font-size: 1.1em;
    //text-shadow: 2px 2px #000;
    //font-family: $baloo;
    font-weight: $bold;
    .fixed &{
      text-shadow: none;
    }
  }
}
.main-nav-toggle{
  @include navToggle(2em,#fff);
  @include showTo($bp)
}
