*,
*::before,
*:after {
  box-sizing: border-box;
}

img{
  max-width: 100%;
  display: block;
  height: auto;
}


body {
  margin : 0;
  font-family : $main-font;
  font-size: 16px;
  font-weight: normal;
}

h1,h2,h3,h4,h5,h6{
  font-family: $baloo;
  font-weight: $bold;
  margin: 0;
}

h1{
  font-size: 1.7em;
  line-height: 2em;
}

h2{
  font-size: 1.5em;
  line-height: 1.8em;
}

h3{
  font-size: 1.3em;
  line-height: 1.6em;
}

h4{
  font-size: 1.2em;
  line-height: 1.5em;
}

p{
  font-size: 1em;
  line-height: 1.1em;
  margin: 0;
  margin-bottom: .5em;
  &:last-child, &:nth-child(only){
    margin-bottom: 0;
  }
}
